.header {
  grid-area: h;
  background-color: black;
  position: fixed;
  width: 100%;
  z-index: 1;
  transition: 0.5s
}

.header span {
  cursor: pointer;
}

.header .login {
  color: white;
  text-decoration: none;
  float: right;
  padding: 20px;
}

.header a {
  color: white;
}

.headerImg {
  float: left;
  width: 150px;
  height: 80px;
}