.login {
    text-align: left;
    margin-left: 40%;
    margin-top: 100px;
    min-height: calc(100vh - 183px);
}

.error {
    color: red;
    font-style: italic;
    height: 140px;
}

.login input {
    height: 25px;
    border-radius: 10px;
    margin: 5px;
}

.login .form span {
    color: red;
    font-style: italic;
}

.login .rememberMe {
    margin-left: 7%;
}

.login button {
    margin-left: 9%;
    width: 100px;
    height: 30px;
}